.contact {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.btn {
  width: 50px;
  height: 18px;
  margin-left: 25px;
  font-size: 13px;
  border-radius: 4px;
  border: 1px solid #d3d3d3;
  background-color: white;
}

.btn:hover {
  background-color: #0366d6;
  border: 2px solid #0366d6;
  cursor: pointer;
}
